
/* eslint-disable indent,camelcase */

import XInput from 'vux/src/components/x-input/index.vue'
import Group from 'vux/src/components/group/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'
import Box from 'vux/src/components/box/index.vue'
import Flexbox from 'vux/src/components/flexbox/flexbox.vue'
import FlexboxItem from 'vux/src/components/flexbox/flexbox-item.vue'

import UtilMixin from '@/mixins/UtilMixin.vue'
import UploadImg from '@/components/uploadImg/uploadImg.vue'
import FormMixin from '@/mixins/FormMixin.vue'

export default {
mixins: [UtilMixin, FormMixin],
data() {
return {
groupColor: '#50b97b',
name: null,
tel: null,
identity: null,
zhima_score: null,
wechat: null,
address: null,
idcard_pic: '',
idcard_backpic: '',
idcard_people_pic: '',
type: 0
}
},
components: {
XInput,
Group,
XButton,
Box,
Flexbox,
FlexboxItem,
UploadImg
},
computed: {},
methods: {
changeImg(data, nodelName) {
if (nodelName !== 'undefined') {
this[nodelName] = data
this.$vux.toast.show({
text: '上传成功',
time: 500
})
}
},
submit() {
this.formMixin_submit('/update_base_info/')
.then(() => {
const _this = this
this.$vux.toast.show({
text: '提交成功',
time: 500,
onHide () {
_this.routerLink('ApplySupplementInfo', {}, {checkway: _this.$route.query.checkway})
}
})
})
.catch((err) => {
console.log(`code:${err.code} \n msg:${err.msg}`)
})
},
checkInfo() {
this.$axios.post({
url: '/check_base_info/',
data: {
customer_id: parseInt(localStorage.getItem('yunhu!customer_id'))
}
}).then(res => {
this.name = res.name
this.tel = res.tel
this.identity = res.identity
this.zhima_score = res.zhima_score
this.wechat = res.wechat
this.address = res.address

this.idcard_pic = res.idcard_pic
this.idcard_backpic = res.idcard_backpic
this.idcard_people_pic = res.idcard_people_pic
})
}
},
created() {
this.checkInfo()
},
mounted() {
}
}



